    constructor(props) {
        super(props);
        this.state = {
            Drivers: [],
            pickupcount: {},
            coliscount: {},
            exchangefromcount: {},
            exchangetocount: {},


        };
    }

    componentDidMount() {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        var countPackages = axios.get(
            urlbackend + "/admin/pickup/count",
            config
        );
        axios
            .get(urlbackend + "/driver", config)
            .then((response) => {
                //console.log("this respon list", response.data);
                this.setState({ Drivers: response.data.data });
            })
            .catch((e) => {
                //console.log(e);
            });



        var getcolis = axios.get(urlbackend + "/admin/colis/count", config);
        var getexchangefromcount = axios.get(urlbackend + "/console_from/count", config);
        var getexchangetocount = axios.get(urlbackend + "/console_to/count", config);

        axios
            .all([countPackages, getcolis, getexchangefromcount, getexchangetocount])
            .then(
                axios.spread((...response) => {
                    console.log("this responconsole list0", response[0].data);
                    this.setState({ pickupcount: response[0].data })
                    this.setState({ coliscount: response[1].data })
                    this.setState({ exchangefromcount: response[2].data })
                    this.setState({ exchangetocount: response[3].data })

                })
            )
            .catch((errors) => {
                // react on errors.
                console.error(errors);
            });
    }
