import React, { useState } from "react";
import { Modal, ModalHeader, ModalBody, } from "reactstrap";
import Tracking from "./Tracking";

const ModalTracking = (props) => {
  const { className } = props;

  const [modal, setModal] = useState(false);
  const [unmountOnClose] = useState(true);

  const toggle = () => setModal(!modal);

  return (
    <div>
      <button onClick={toggle} className="label theme-bg2 text-white f-15"> Voir position</button>
     
      <Modal
        size="xl"
        heigth="500px"
        isOpen={modal}
        toggle={toggle}
        className={className}
        unmountOnClose={unmountOnClose}
      >
        <ModalHeader toggle={toggle}>positions chauffeur</ModalHeader>
        <ModalBody>
        <div key={props.key} style={{ height: '80vh', width: '100%' }}>
            
         <Tracking fortracking={props.fortracking}  locationto={props.locationto}></Tracking>
         </div>
        </ModalBody>
      </Modal>
    </div>
  );
};

export default ModalTracking;
